import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Component } from '@angular/core';
import { ToastController, ViewController, IonicPage, NavController, NavParams, AlertController, Platform } from 'ionic-angular';
import * as moment from 'moment';
import { Calendar } from '@ionic-native/calendar';
import { User } from '../../providers/user/user';
import { CallNumber } from '@ionic-native/call-number';
import { SocialSharing } from '@ionic-native/social-sharing';



@IonicPage()
@Component({
  selector: 'page-calendar',
  templateUrl: 'calendar.html',
})
export class CalendarPage {

  public eventList: any = [];
  public selectedEvent: any;
  public isSelected = true;


  ///


  public date: any;
  public daysInThisMonth: any;
  public daysInLastMonth: any;
  public daysInNextMonth: any;
  public monthNames: any = [];
  public currentMonth: any;
  public currentYear: any;
  public currentDate: any;



  contactUser: FormGroup;

  constructor(private platform: Platform, private socialSharing: SocialSharing,private callNumber: CallNumber, private alertCtrl: AlertController, public user: User, private toastCtrl: ToastController, private calendar: Calendar, public navCtrl: NavController, public navParams: NavParams, private fb: FormBuilder, public viewCtrl: ViewController) {

    this.monthNames = ['Enero', 'Febrero', 'Marzo', 'Abril', 'Mayo', 'Junio', 'Julio', 'Agosto', 'Septiembre', 'Octubre', 'Noviembre', 'Diciembre'];

    this.date = new Date();

    /**Se agrega la pagina a google analitycs */

    /** Se crea el verificador de formularios*/
    this.contactUser = this.fb.group({
      name: ['', [Validators.required]],
      email: ['', [Validators.required, Validators.email]],
      phone: ['', [Validators.nullValidator]],
      message: ['', [Validators.required]]
    });



  }

  doRefresh(refresher) {


    let response = this.user.getTask();

    Promise.all([response]).then(data => {
      refresher.complete();
      this.getDaysOfMonth();
      this.eventList = this.user.userTask;
    });
  }

  call(number) {

    //console.log(number);
    this.callNumber.callNumber(number, true)
      .then(() => { })
      .catch(()=>{} );

  }
  mail(email, fullname) {

    //console.log(email, fullname);
    this.socialSharing.shareViaEmail('Hola ' + fullname, 'Asunto', [email]).then(() => {

    
    }).catch(() => {

      
    });

  }
  whatsapp(number, fullname) {



    this.platform.ready().then(() => {

      /** Compartir la propiedad por whatsapp */
      this.socialSharing.shareViaWhatsAppToReceiver(number, '').then(() => {
        // Success!
      }).catch(() => {
        // Error!
      });
    });






  }
  
  ionViewDidEnter() {
      //console.log(this.navParams,2);

    
      this.getDaysOfMonth();
      this.eventList = this.user.userTask;
  
  }

  loadEventThisMonth() {

    this.eventList = new Array();

    var startDate = new Date(this.date.getFullYear(), this.date.getMonth(), 1);
    var endDate = new Date(this.date.getFullYear(), this.date.getMonth() + 1, 0);



    this.calendar.listEventsInRange(startDate, endDate).then(
      (msg) => {
        msg.forEach(item => {

          this.eventList.push(item);
        });
      },
      (err) => {
      }
    );

  }

  selectDate(day) {

    var addd = '';
    var add = '';

    if (day < 10) {
      addd = '0';
    }

    if (this.date.getMonth() < 10) {
      add = '0';
    }

    this.isSelected = false;
    this.selectedEvent = new Array();
    var thisDate1 = this.date.getFullYear() + "-" + add + (this.date.getMonth() + 1) + "-" + addd + day + "";
    var thisDate2 = this.date.getFullYear() + "-" + add + (this.date.getMonth() + 1) + "-" + addd + day + "";
    this.eventList.forEach(event => {

      if (((event.startdate >= thisDate1) && (event.startdate <= thisDate2)) || ((event.finishdate >= thisDate1) && (event.finishdate <= thisDate2))) {
        this.isSelected = true;
        this.selectedEvent.push(event);
      }

    });
  }


  checkEvent(day) {

    var hasEvent = false;
    var add = '';
    var addd = '';
    if (this.date.getMonth() < 10) {
      add = '0';
    }

    if (day < 10) {
      addd = '0';
    }

    var thisDate1 = this.date.getFullYear() + "-" + add + (this.date.getMonth() + 1) + "-" + addd + day + "";
    var thisDate2 = this.date.getFullYear() + "-" + add + (this.date.getMonth() + 1) + "-" + addd + day + "";


    this.user.userTask.forEach(event => {
      if (((event.startdate >= thisDate1) && (event.startdate <= thisDate2)) || ((event.finishdate >= thisDate1) && (event.finishdate <= thisDate2))) {
        hasEvent = true;

      }
    });

    return hasEvent;
  }






  getDaysOfMonth() {
    var i = 0;
    this.daysInThisMonth = new Array();
    this.daysInLastMonth = new Array();
    this.daysInNextMonth = new Array();

    var nM = this.date.getMonth();
    var cr = new Date().getMonth();
    this.currentMonth = this.monthNames[nM];

    this.currentYear = this.date.getFullYear();

    if (nM === cr) {
      this.currentDate = new Date().getDate();
    } else {
      this.currentDate = 999;
    }

    var firstDayThisMonth = new Date(this.date.getFullYear(), this.date.getMonth(), 1).getDay();
    var prevNumOfDays = new Date(this.date.getFullYear(), this.date.getMonth(), 0).getDate();
    for (i = prevNumOfDays - (firstDayThisMonth - 1); i <= prevNumOfDays; i++) {
      this.daysInLastMonth.push(i);
    }

    var thisNumOfDays = new Date(this.date.getFullYear(), this.date.getMonth() + 1, 0).getDate();
    for (i = 0; i < thisNumOfDays; i++) {
      this.daysInThisMonth.push(i + 1);
    }

    var lastDayThisMonth = new Date(this.date.getFullYear(), this.date.getMonth() + 1, 0).getDay();
    var nextNumOfDays = new Date(this.date.getFullYear(), this.date.getMonth() + 2, 0).getDate();
    for (i = 0; i < (6 - lastDayThisMonth); i++) {
      this.daysInNextMonth.push(i + 1);
    }
    var totalDays = this.daysInLastMonth.length + this.daysInThisMonth.length + this.daysInNextMonth.length;
    if (totalDays < 36) {
      for (i = (7 - lastDayThisMonth); i < ((7 - lastDayThisMonth) + 7); i++) {
        this.daysInNextMonth.push(i);
      }
    }
  }


  goToLastMonth() {
    this.date = new Date(this.date.getFullYear(), this.date.getMonth(), 0);
    this.getDaysOfMonth();
  }

  goToNextMonth() {
    this.date = new Date(this.date.getFullYear(), this.date.getMonth() + 2, 0);
    this.getDaysOfMonth();
  }


  addEvent() {
   
    this.navCtrl.push('AddTaskPage').then(() => {

      this.navCtrl.getActive().onDidDismiss(data => {
        //console.log('actualizando tareas');
       let promise = this.user.getTask();
        Promise.all([promise]).then(data => {
        this.getDaysOfMonth();
        this.eventList = this.user.userTask;
        });
      });

    });
  }

  deleteEvent(evt) {

    let alert = this.alertCtrl.create({
      title: 'Confirm Delete',
      message: 'Are you sure want to delete this event?',
      buttons: [
        {
          text: 'Cancel',
          role: 'cancel',
          handler: () => {
          }
        },
        {
          text: 'Ok',
          handler: () => {
            this.calendar.deleteEvent(evt.title, evt.location, evt.notes, new Date(evt.startDate.replace(/\s/, 'T')), new Date(evt.endDate.replace(/\s/, 'T'))).then(
              (msg) => {
                this.loadEventThisMonth();
                this.selectDate(new Date(evt.startDate.replace(/\s/, 'T')).getDate());
              },
              (err) => {
              }
            )
          }
        }
      ]
    });
    alert.present();
  }



  submit() {


    /** Se genera el correo y se muestra el mensaje de correo enviado*/
    //this.control.infoRequest(this.contactUser.value);



    let toast = this.toastCtrl.create({
      message: 'Enviado',
      duration: 3000,
      position: 'top',
    });
    toast.present();

    /** Se cierra la pagina */
    this.viewCtrl.dismiss();
  }


}
  


